.snippets__desktop-img {
  position: relative;
  background-image: url("../images/image-computer.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  height: 17.9375rem;
  margin-bottom: 3.875rem;
}
@media (min-width: 40em) {
  .snippets__desktop-img {
    margin: 0;
    background: none;
    height: 35.75rem;
  }
  .snippets__desktop-img::after {
    content: "";
    position: absolute;
    background-image: url("../images/image-computer.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}
@media (min-width: 71.875em) {
  .snippets__desktop-img {
    margin: 0;
    background: none;
    height: 35.75rem;
  }
  .snippets__desktop-img::after {
    content: "";
    position: absolute;
    background-image: url("../images/image-computer.png");
    background-repeat: no-repeat;
